@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap");
.header {
  width: 100%;
  height: 8rem;
  background: #f5f5f5;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  z-index: 20;
  transition: all 0.3s cubic-bezier(0.39, 0.575, 0.565, 1);
}
.header .icon__menu {
  width: 8rem;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 1rem;
}
.header .icon__menu i {
  font-size: 2rem;
  cursor: pointer;
  position: absolute;
  color: #3b5895;
}
.header h1 {
  color: #3b5895;
}
@media (max-width: 480px) {
  .header h1 {
    font-size: 2.4rem;
  }
}
.header .logo {
  width: 15rem;
  height: 6rem;
  margin-right: 3rem;
}
@media (max-width: 768px) {
  .header .logo {
    width: 10rem;
  }
}
@media (max-width: 480px) {
  .header .logo {
    width: 8rem;
    height: 4.5rem;
  }
}

.menu__side {
  width: 8rem;
  height: 100%;
  background: #3b5895;
  position: fixed;
  top: 8rem;
  left: 0;
  font-size: 1.8rem;
  z-index: 21;
  overflow: hidden;
  overflow-y: scroll;
  border-right: 2rem solid #3b5895;
  transition: all 0.3s cubic-bezier(0.39, 0.575, 0.565, 1);
  box-shadow: 14px -1px 4px -11px rgba(0, 0, 0, 0.59);
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.menu__side .name__page {
  color: #ffffff;
  padding: 2rem 3rem;
  display: flex;
  align-items: center;
}
.menu__side::-webkit-scrollbar {
  display: none;
}
.menu__side.side__move {
  width: 25rem;
}
@media (max-width: 768px) {
  .menu__side {
    left: -25rem;
    width: 25rem;
  }
  .menu__side.side__move {
    left: 0rem;
  }
}

.barra__progreso {
  padding: 2rem 3rem;
}
.barra__progreso .barra {
  width: 100%;
  height: 0.3rem;
  background: #ffffffb2;
  margin-bottom: 1rem;
  position: relative;
  display: none;
}
.barra__progreso .barra.barra_ver {
  display: block;
}
.barra__progreso .barra .porcentaje {
  position: absolute;
  background: #ffffff;
  width: 75%;
  height: 100%;
  top: 0;
  left: 0;
}
.barra__progreso #progreso__mensaje {
  font-size: 2rem;
  color: #ffffff;
}

.options__menu {
  padding: 2rem 3rem;
  position: absolute;
  top: 18rem;
}
.options__menu a {
  color: #ffffffb2;
  cursor: default;
  display: block;
  position: relative;
  transition: all 0.3s ease;
}
.options__menu a:hover {
  color: #ffffff;
}
.options__menu a.select {
  color: #ffffff;
}
.options__menu a.select::before {
  content: "";
  width: 0.3rem;
  height: 80%;
  position: absolute;
  top: 10%;
  left: -3rem;
  background: #ffffff;
}
.options__menu a .option {
  padding: 2rem 0;
  display: flex;
  align-items: center;
  position: relative;
}
.options__menu a .option i {
  cursor: pointer;
}
.options__menu a .option h4 {
  font-weight: 300;
  cursor: pointer;
}

.btn_nav {
  position: fixed;
  width: 30rem;
  height: 6rem;
  right: 10rem;
  bottom: 0;
  left: 40%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  background: rgba(61, 137, 199, 0.699);
  border-top-left-radius: 2rem;
  border-top-right-radius: 2rem;
  z-index: 100;
}
@media (max-width: 768px) {
  .btn_nav {
    left: 30%;
  }
}
@media (max-width: 480px) {
  .btn_nav {
    left: 10%;
  }
}
.btn_nav i {
  width: auto;
  margin: 0;
  font-size: 4.2rem;
  color: #ffffff;
  cursor: pointer;
  transition: all 0.2s ease-in;
}
.btn_nav i:hover {
  color: #3b5895;
}

.contenedor #titulo_01 {
  color: #2c4268;
  text-align: center;
}
.contenedor #parrafo_01 {
  font-size: 2rem;
  color: #e25327;
  text-align: justify;
  margin-bottom: 2rem;
}
.contenedor .cont_img01 {
  width: 30rem;
}

#intro_actividad {
  width: 100%;
  height: 60rem;
  overflow: hidden;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row-reverse;
  background-image: linear-gradient(to top, #129a64e3 0%, #129a64e3 100%), url("/build/img/contenido/Pag03/fondo.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (max-width: 768px) {
  #intro_actividad {
    flex-direction: column;
    background: #616a84;
    height: 55rem;
  }
}
#intro_actividad #vineta {
  width: 60rem;
  background: #ffffff;
  padding: 2.4rem;
  border-radius: 2rem;
  border: 5px solid #3b5895;
  font-size: 2.4rem;
  text-align: center;
  position: absolute;
  top: 6rem;
  left: 20rem;
}
@media (max-width: 1280px) {
  #intro_actividad #vineta {
    width: 50rem;
    left: 6rem;
  }
}
@media (max-width: 960px) {
  #intro_actividad #vineta {
    left: 3rem;
    width: 30rem;
    font-size: 1.8rem;
  }
}
@media (max-width: 768px) {
  #intro_actividad #vineta {
    margin-top: 5rem;
    position: static;
  }
}
#intro_actividad #vineta #relativo {
  position: relative;
}
#intro_actividad #vineta #relativo #frase_01 {
  color: #3b5895;
  font-weight: 700;
}
#intro_actividad #vineta #relativo #frase_02 {
  color: #129a63;
  font-weight: 700;
}
#intro_actividad #vineta #relativo::after {
  content: "";
  position: absolute;
  background: #3b5895;
  top: 50%;
  right: -5.6rem;
  width: 3rem;
  height: 4rem;
  clip-path: polygon(0 0, 0% 100%, 100% 50%);
}
@media (max-width: 768px) {
  #intro_actividad #vineta #relativo::after {
    top: 120%;
    left: 45%;
    clip-path: polygon(0 0, 50% 100%, 100% 0);
    width: 4rem;
    height: 3rem;
  }
}
#intro_actividad .cont-imagen {
  width: 40%;
  height: 100%;
  padding: 2rem 2rem 0 2rem;
  background: #616a84;
}
@media (max-width: 1280px) {
  #intro_actividad .cont-imagen {
    width: 50%;
  }
}
@media (max-width: 960px) {
  #intro_actividad .cont-imagen {
    width: 75%;
  }
}
@media (max-width: 768px) {
  #intro_actividad .cont-imagen {
    width: 100%;
    height: 50%;
  }
}

#zona_actividad01 {
  width: 100%;
  padding: 10rem 2rem 2rem 2rem;
  background-image: url(/build/img/contenido/Pag03/fondo.svg);
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 1.5rem;
}
#zona_actividad01 #pines {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
}
#zona_actividad01 #pines .definiciones {
  width: 10rem;
  height: 4.5rem;
  position: relative;
  background: #ffffff;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  margin-bottom: 3.2rem;
  box-shadow: 10px 11px 5px -4px rgba(0, 0, 0, 0.75);
  transition: animation 0.3s ease-in-out;
  z-index: 12;
}
#zona_actividad01 #pines .definiciones:hover {
  cursor: pointer;
  animation: 1s balanceo 1;
}
#zona_actividad01 #pines .definiciones p {
  font-size: 1rem;
  text-align: center;
  font-weight: 700;
  padding-bottom: 0.5rem;
}
#zona_actividad01 #pines .definiciones .pin {
  position: absolute;
  z-index: 11;
  width: 3.2rem;
  height: 3.2rem;
  top: -1.2rem;
  left: 30%;
}
#zona_actividad01 #pines .definiciones::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 1rem;
  top: 0;
  left: 0;
}
#zona_actividad01 #pines .definiciones#definicion01::before {
  background: #e25327;
}
#zona_actividad01 #pines .definiciones#definicion02::before {
  background: #3d3d3d;
}
#zona_actividad01 #pines .definiciones#definicion03::before {
  background: #2c4268;
}
#zona_actividad01 #pines .definiciones#definicion04::before {
  background: #f9c930;
}
#zona_actividad01 #pines .definiciones#definicion05::before {
  background: #2c4268;
}
#zona_actividad01 #pines .definiciones#definicion06::before {
  background: #f9c930;
}
#zona_actividad01 #pines .definiciones#definicion07::before {
  background: #616a84;
}
#zona_actividad01 #pines .definiciones#definicion08::before {
  background: #e25327;
}
#zona_actividad01 #post-its {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
}
#zona_actividad01 #post-its .posts {
  width: 25rem;
  height: 15rem;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 0 1.6rem;
  font-size: 1.2rem;
  font-weight: 700;
  color: #3b5895;
  margin-bottom: 3rem;
  position: relative;
  background-position: center bottom;
  background-repeat: no-repeat;
  background-size: cover;
}
#zona_actividad01 #post-its .posts .clip {
  position: absolute;
  width: 3rem;
  height: 5rem;
  top: -0.5rem;
  right: -1rem;
  z-index: 4;
}
#zona_actividad01 #post-its .posts p {
  margin-top: 2rem;
}
#zona_actividad01 #post-its .posts#post01 {
  background-image: url(/build/img/contenido/Pag03/post-it1.svg);
}
#zona_actividad01 #post-its .posts#post02 {
  background-image: url(/build/img/contenido/Pag03/post-it2.svg);
}
#zona_actividad01 #post-its .posts#post03 {
  background-image: url(/build/img/contenido/Pag03/post-it3.svg);
}
#zona_actividad01 #post-its .posts#post04 {
  background-image: url(/build/img/contenido/Pag03/post-it4.svg);
}
#zona_actividad01 #post-its .posts#post05 {
  background-image: url(/build/img/contenido/Pag03/post-it5.svg);
}
#zona_actividad01 #post-its .posts#post06 {
  background-image: url(/build/img/contenido/Pag03/post-it6.svg);
}
#zona_actividad01 #post-its .posts#post07 {
  font-size: 1rem;
  background-image: url(/build/img/contenido/Pag03/post-it7.svg);
}
#zona_actividad01 #post-its .posts#post08 {
  background-image: url(/build/img/contenido/Pag03/post-it8.svg);
}

@keyframes balanceo {
  20% {
    transform: rotate(8deg);
  }
  40%, 80% {
    transform: rotate(-8deg);
  }
  60%, 95% {
    transform: rotate(8deg);
  }
}